body {
  margin: 0;
  padding: 0;
  background-color: #2B354C;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-container {
    position: fixed;
    margin-top: -6%;
    z-index: 999;
}

@media screen and (max-width: 600px) {
    .navbar-container {
        margin-top: -40%;
    }
}

.navbar-toggle {
    margin-left: 1%;
}

.navbar-dropdown {
    overflow: hidden;
    margin-left: 25%;
    width: 120px;
    transition: max-height 0.5s ease-in-out;
}

.navbar-link {
    text-align: left;
    color: #748DA5;
    font-size: 1.6em;
    font-weight: normal;
    margin-top: 8%;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.navbar-link:hover {
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
}
.splashpage {
    /* margin-bottom: -25%; */
}

.datathon-logo {
    margin-top: 8%;
}

.date {
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    color: #748DA5;
    text-align: center;
}

.intro-link {
    text-align: center;
    border: 1px solid #2B354C;
    padding: 0px;
    color: #748DA5;
    transition: border-color 0.4s ease-in-out, color 0.4s ease-in-out;
}

.intro-link:hover {
    border-color: #ffffff;
    color: #ffffff;
}

.intro-link-text {
    font-family: 'Montserrat', sans-serif;
    padding: 12px;
    margin: 0px;
    font-weight: normal;
    font-size: 1.8em;
}

@media screen and (max-width: 500px){
    .datathon-logo {
        margin-top: 50%;
    }

    .intro-link {
        opacity: 0.7;
    }

    .intro-link-text {
        font-size: 1.2em;
    }
}

.title {
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
}

.title-theme {
    color: #2B354C;
    border: solid #2B354C;
    border-color: #2B354C rgb(94, 116, 136) rgb(94, 116, 136) #2B354C;
    border-width: 4px 2px 2px 4px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 3em;
    font-weight: normal;
}

.title-alternate {
    color: #A7BBCE;
    border: solid #A7BBCE;
    border-color: #A7BBCE rgb(94, 116, 136) rgb(94, 116, 136) #A7BBCE;
    border-width: 4px 2px 2px 4px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 3em;
}

@media screen and (max-width: 500px){
    .title {
        margin-top: 15%;
    }
    .title-theme {
        font-size: 1.5em;
    }

    .title-alternate {
        font-size: 1.5em;
    }
}
.about-page {
    padding: 1% 0 5% 0;
    background-color: #A7BBCE;
}

.about-content {
    margin-left: auto;
    margin-right: auto;
    margin-top: 4%;
}

.about-content-card {
    margin-left: 3%;
    margin-right: 3%;
}

.about-content-text {
    text-align: left;
    color: #2B354C;
    font-size: 1.2em;
}
.schedule-page {
    padding: 1% 0 5% 0;
}

.schedule-items {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 4%;
    color: #A7BBCE;
    font-weight: normal;
}
.faqs-page {
    width: 100%;
    height: 100%;
    background-color: #A7BBCE;
    overflow: hidden;
}

.questions { 
    margin-top: 3%;
    margin-bottom: 6%;
}

.dropdown-container {
    padding: 2%;
    transition: background-color 0.4s ease-in-out;
    color: #2B354C;
}

.dropdown-question {
    font-size: 2em;
}

.dropdown-answer {
    margin-top: 2%;
    overflow: hidden;
    transition: max-height 0.4s ease-in-out;
    font-size: 1.7em;
}
.App {
  text-align: center;
}
